import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { FiFileText } from "react-icons/fi";
import { IoMdCloudUpload } from "react-icons/io";
import Alert, { AlertInterface } from "../../../../components/Alert/Alert";
import Loading from "../../../../components/Loading/Loading";
import {
  course,
  QuestionInterface,
  uploadQuestionsResponse,
} from "../../../../interfaces";
import { formErrorInterface } from "../../../../interfaces/form";
import { API } from "../../../../utils/api";
import { connect } from "react-redux";
import {
  Auth,
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  setUploadedQuestions,
  questionTypesStoreInterface,
  loadQuestionTypes,
  loadQuestionBank,
} from "../../../../actions";
import { StoreState } from "../../../../reducers";
import UploadedQuestions from "./UploadedQuestions/UploadedQuestions";
import EditQuestion from "./EditQuestion/EditQuestion";

interface UploadInterface {
  selectedCourse: course;
  questionBank: QuestionBankStoreInterface;
  course: courseStoreInterface;
  questionTypes: questionTypesStoreInterface;
  setUploadingStatus: (status: boolean) => void;
  setSelectedCourse: (course: course | null) => void;
  setUploadedQuestions: (questions: uploadQuestionsResponse | null) => void;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  loadQuestionBank: (
    course_id: string,
    callback: (loading: boolean) => void
  ) => void;
}

const _Upload = (props: UploadInterface) => {
  const [questions, setQuestions] = useState<File | null>(null);
  const [formError, setFormError] = useState<formErrorInterface | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [upload_percentage, set_upload_percentage] = useState<number>(0);
  const [showQuestionEdit, setShowQuestionEdit] = useState<{
    status: boolean;
    question: QuestionInterface | null;
  } | null>(null);

  const validateExcelFile = async (file: File) => {
    if (
      file.type !==
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" &&
      file.type !== "application/vnd.ms-excel" &&
      file.type !== "text/csv"
    ) {
      return setFormError({
        element: "questions",
        msg: "Invalid file, Please choose excel file or svg file",
      });
    } else {
      return null;
    }
  };

  const submitQuestion = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // Validation
    setFormError(null);
    if (questions === null) {
      return setFormError({
        element: "questions",
        msg: "Please select a file",
      });
    } else if (questions.size === 0) {
      return setFormError({
        element: "questions",
        msg: "Invalid file, Please choose a real file",
      });
    } else if (
      questions.type !==
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" &&
      questions.type !== "application/vnd.ms-excel" &&
      questions.type !== "text/csv"
    ) {
      return setFormError({
        element: "questions",
        msg: "Invalid file, Please choose excel file or svg file",
      });
    } else {
      setLoading(true);
      props.setUploadingStatus(true);
      try {
        const formData = new FormData();
        formData.append("course", props.selectedCourse.course_id);
        formData.append("file", questions);
        const res = await axios.post(`${API.QBank}/question`, formData, {
          onUploadProgress: (progressEvent: any) => {
            let number_percentage =
              (+progressEvent?.loaded / +progressEvent.total) * 100;
            set_upload_percentage(number_percentage);
          },
        });
        if (res.status === 201) {
          // console.log("Uploaded questions: ", res.data);
          props.setUploadedQuestions(res.data);
        }
        // console.log("Questions: ", res);
      } catch (error: any) {
        console.log("Err: ", { ...error });
        if (error.response) {
          if (error.response.data) {
            if (error.response.data.errors) {
              setFormError({
                element: "questions",
                msg: error.response.data.errors[0].message,
              });
            }
          }
        }
      }
      setLoading(false);
      props.setUploadingStatus(false);
    }
  };

  const selectQuestion = (question: QuestionInterface) => {
    setShowQuestionEdit({
      status: true,
      question: question,
    });
  };

  const unSelectQuestion = () => {
    setShowQuestionEdit({
      status: false,
      question: null,
    });
  };

  useEffect(() => {
    if (props.questionBank.questions === null) {
      if (props.course.selected_course !== null) {
        props.loadQuestionBank(
          props.course.selected_course.course_id,
          setLoading
        );
      }
    }
  }, [props]);

  return (
    <Fragment>
      {props.questionBank.uploadedQuestions === null ? (
        <div className="mb-4">
          <div className="flex flex-row justify-between items-center rounded px-3 py-2 bg-white animate__animated animate__fadeIn shadow">
            <div className="flex flex-col">
              <span className="font-bold text-blue-600 text-lg truncate">
                {props.course.selected_course !== null &&
                  props.course.selected_course.course_name}
              </span>
              <span className="text-xs font-bold text-gray-500 truncate">
                Course code:{" "}
                {props.course.selected_course &&
                  props.course.selected_course.course_code}
              </span>
            </div>
            {/* <div className="btn bg-blue-50 text-blue-500 hover:bg-blue-100 hover:text-blue-700  cursor-pointer">Download template</div> */}
          </div>
          <form onSubmit={submitQuestion}>
            <div className={`my-2`}>
              <label
                className={`w-full flex flex-col items-center px-4 py-6 tracking-wide rounded border border-gray-400 bg-white ${
                  loading === true
                    ? "cursor-not-allowed hover:bg-gray-100 bg-gray-100 text-gray-400"
                    : "cursor-pointer hover:border-blue-600 hover:bg-blue-100 border-gray-100 text-blue-600 hover:text-blue-700"
                }`}
              >
                <IoMdCloudUpload className="text-6xl" />
                <span className="text-base leading-normal">
                  {loading === true
                    ? "Loading, Please wait..."
                    : "Click to select a file"}
                </span>
                {loading === false && (
                  <input
                    onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                      setFormError(null);
                      if (
                        e.target.files &&
                        e.target.files.length > 0 &&
                        loading === false
                      ) {
                        setQuestions(e.target.files[0]);
                        validateExcelFile(e.target.files[0]);
                      }
                    }}
                    type="file"
                    className="hidden"
                  />
                )}
              </label>
            </div>
            {questions !== null && (
              <div className="rounded bg-blue-50 px-3 py-2 flex flex-row items-center justify-between gap-2 border border-blue-500">
                <div className="flex flex-row gap-2 items-center">
                  <FiFileText className="text-5xl text-blue-600" />
                  <div className="flex flex-col">
                    <span className="font-bold text-blue-600 truncate">
                      File: {questions.name}
                    </span>
                    <span className="text-gray-500 text-xs truncate">
                      File type: {questions.type}
                    </span>
                    <span className="text-gray-500 text-xs">
                      Size: {Math.round(questions.size / 1024)} Kbs
                    </span>
                  </div>
                </div>
                {loading === false && (
                  <div
                    onClick={() => {
                      setQuestions(null);
                      setFormError(null);
                    }}
                    className="btn text-sm bg-red-50 text-red-600 border border-red-200 cursor-pointer hover:bg-red-100"
                  >
                    Delete
                  </div>
                )}
              </div>
            )}
            {formError !== null && formError.element === "questions" && (
              <div onClick={() => setFormError(null)} className="mt-2">
                <Alert
                  title="Invalid"
                  description={formError.msg}
                  theme={AlertInterface.danger}
                />
              </div>
            )}
            {loading === true && questions !== null && (
              <div className="bg-blue-200 h-6 w-full rounded mt-2">
                <div
                  className="h-full rounded bg-blue-500 flex flex-row items-center justify-between px-2 truncate text-xs text-white font-bold animate__animated animate__delay-1s"
                  style={{ width: `${upload_percentage}%` }}
                >
                  <span className="italic">Uploading, Please wait...</span>
                  <span>{upload_percentage}%</span>
                </div>
              </div>
            )}
            {loading === true ? (
              <div className="mt-5">
                <Loading />
              </div>
            ) : (
              <div className="flex flex-row items-center justify-between my-2">
                <div
                  onClick={() => props.setSelectedCourse(null)}
                  className="btn cursor-pointer bg-gray-100"
                >
                  Back
                </div>
                <button
                  type="submit"
                  className="btn cursor-pointer bg-blue-600 text-white"
                >
                  Save questions
                </button>
              </div>
            )}
          </form>
        </div>
      ) : showQuestionEdit !== null &&
        showQuestionEdit.status === true &&
        showQuestionEdit.question !== null &&
        props.questionBank.uploadedQuestions?.insertedData ? (
        <EditQuestion
          selectedQuestion={showQuestionEdit.question}
          questionsList={props.questionBank.uploadedQuestions.insertedData}
          unSelectQuestion={unSelectQuestion}
        />
      ) : (
        <UploadedQuestions
          selectedCourse={props.selectedCourse}
          selectQuestion={selectQuestion}
          questionTypes={props.questionTypes}
          loadQuestionTypes={props.loadQuestionTypes}
        />
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
  questionTypes,
}: StoreState): {
  auth: Auth;
  course: courseStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionBank: QuestionBankStoreInterface;
  questionTypes: questionTypesStoreInterface;
} => {
  return {
    auth: auth,
    course: course,
    schoolLevels: schoolLevels,
    questionBank: questionBank,
    questionTypes: questionTypes,
  };
};

const Upload = connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  setUploadedQuestions,
  loadQuestionTypes,
  loadQuestionBank,
})(_Upload);

export default Upload;
