import React from "react";

interface DonwloadLinkProps {
  className: string;
  title: string;
  caption: string;
  location: string;
  icon: any;
}
const DownloadLink = (props: DonwloadLinkProps) => {
  const downloadFile = () => {
    window.location.href = props.location;
  };
  return (
    <button
      title={props.title}
      className={`flex flex-row items-center gap-2 ${props.className}`}
      onClick={downloadFile}
    >
      {props.icon}
      {props.caption}
    </button>
  );
};

export default DownloadLink;
