import React, { useEffect, useState } from "react";
import Loading from "../../../../../components/Loading/Loading";
import { connect } from "react-redux";
import {
  Auth,
  courseStoreInterface,
  loadCourses,
  schoolLevelsStoreInterface,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  QuestionBankStoreInterface,
  setUploadedQuestions,
  questionTypesStoreInterface,
  setSelectedCourse,
  PrepareExamsStoreInterface,
  addExamQuestion,
  IncrementQuestionBank,
} from "../../../../../actions";
import { StoreState } from "../../../../../reducers";
import { course } from "../../../../../interfaces/course";
import {
  QuestionInterface,
  uploadQuestionsResponse,
} from "../../../../../interfaces/question";
import { VscListSelection } from "react-icons/vsc";
import { Redirect } from "react-router-dom";

interface UploadedQuestionsProps {
  questionBank: QuestionBankStoreInterface;
  setUploadedQuestions: (questions: uploadQuestionsResponse | null) => void;
  selectedCourse: course;
  course: courseStoreInterface;
  selectQuestion: (question: QuestionInterface) => void;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
  prepareExam: PrepareExamsStoreInterface;
  addExamQuestion: (question: QuestionInterface) => void;
  IncrementQuestionBank: (question: QuestionInterface) => void;
}

const _UploadedQuestions = (props: UploadedQuestionsProps) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [redirect, setRedirect] = useState<boolean>(false);
  const [setExam, setSetExam] = useState<boolean>(false);
  const returnToCourse = () => {
    if (
      props.questionBank.uploadedQuestions !== null &&
      props.questionBank.uploadedQuestions.insertedData[0].course !==
        props.selectedCourse.course_id
    ) {
      if (props.course.courses) {
        let selected = props.course.courses.find(
          (itm) =>
            props.questionBank.uploadedQuestions &&
            itm.course_id ===
              props.questionBank.uploadedQuestions.insertedData[0].course
        );
        if (selected !== undefined) {
          props.setSelectedCourse(selected);
        }
      }
    }
  };
  const clearUploadedQuestionsStore = () => {
    if (window.confirm("Have you completed to modify questions?")) {
      if (
        props.questionBank.uploadedQuestions !== null &&
        props.questionBank.uploadedQuestions.insertedData.length > 0
      ) {
        for (let selectedQuestion of props.questionBank.uploadedQuestions
          .insertedData) {
          props.IncrementQuestionBank(selectedQuestion);
        }
        props.setUploadedQuestions(null);
      }
      setRedirect(true);
    }
  };
  const addToExam = () => {
    if (window.confirm("Are you sure do you want to add to exam?") === true) {
      if (
        props.questionBank.uploadedQuestions !== null &&
        props.questionBank.uploadedQuestions.insertedData.length > 0
      ) {
        if (props.course.selected_course !== null) {
          for (let x of props.prepareExam.questions) {
            props.addExamQuestion(x);
          }
          if (props.prepareExam.questions.length === 0) {
            for (let itm of props.questionBank.uploadedQuestions.insertedData) {
              props.addExamQuestion(itm);
              props.IncrementQuestionBank(itm);
            }
          }
          if (props.questionBank.uploadedQuestions.insertedData.length !== 0) {
            props.setUploadedQuestions(null);
            setSetExam(true);
          }
        }
      }
    }
  };

  useEffect(() => {
    if (
      props.questionTypes.questionTypes === null ||
      props.questionTypes.questionTypes.length === 0
    ) {
      props.loadQuestionTypes(setLoading);
    }
  }, [props]);
  if (setExam === true) {
    return <Redirect to="/prepare-exam" />;
  }
  if (redirect === true) {
    return <Redirect to="/question-bank" />;
  }
  return (
    <div className="bg-white animate__animated animate__fadeInLeft animate__fast px-2 py-2 gap-3 mb-2 my-4 rounded">
      {props.questionBank.uploadedQuestions !== null &&
      props.questionBank.uploadedQuestions.insertedData[0].course !==
        props.selectedCourse.course_id ? (
        <div className="text-center flex flex-col items-center text-gray-500 py-6 rounded bg-gray-100 mt-5">
          You have changed course! <br />
          <div className="bg-blue-50 text-blue-600 font-bold rounded-full text-center w-max px-3 border border-blue-200 flex items-center justify-center mb-3">
            {props.course.courses !== null &&
              props.course.courses.find(
                (itm) =>
                  itm.course_id ===
                  props.questionBank.uploadedQuestions?.insertedData[0].course
              )?.course_name}
          </div>
          <div
            onClick={() => returnToCourse()}
            className="btn bg-blue-600 text-white cursor-pointer"
          >
            Back to list
          </div>
        </div>
      ) : (
        <>
          <div className="flex flex-row items-center justify-between">
            <div className="text-blue-600 font-bold mb-2 flex flex-row items-center gap-2 text-xl">
              <VscListSelection className="text-2xl" />
              <span>
                Uploaded questions list (
                {props.questionBank.uploadedQuestions?.insertedCount})
              </span>
            </div>
            <div className="flex flex-row items-center gap-2">
              <div
                onClick={() => addToExam()}
                className="btn border bg-blue-50 hover:bg-blue-600 border-blue-600 text-blue-600 hover:text-white mb-2 cursor-pointer"
              >
                Make it an exam
              </div>
              <div
                onClick={() => clearUploadedQuestionsStore()}
                className="btn bg-blue-600 text-white mb-2 cursor-pointer"
              >
                Continue
              </div>
            </div>
          </div>
          {loading === true ? (
            <Loading />
          ) : props.questionBank.uploadedQuestions === null ? (
            <div>Please upload questions</div>
          ) : props.questionBank.uploadedQuestions.insertedData === null ? (
            <Loading />
          ) : props.questionBank.uploadedQuestions.insertedData.length === 0 ? (
            <div>No questions uploaded!</div>
          ) : (
            props.questionBank.uploadedQuestions.insertedData.map((item, i) => (
              <div
                onClick={() => props.selectQuestion(item)}
                key={i + 1}
                className="bg-gray-50 cursor-pointer rounded border border-gray-200 px-3 py-2 hover:bg-blue-50 text-gray-700 hover:text-blue-600 flex flex-row items-center justify-between gap-4 hover:border-blue-600 mb-3"
              >
                <div className="flex flex-row items-center gap-3 w-full truncate">
                  <div className="rounded-full bg-blue-100 text-blue-600 h-8 w-8 flex items-center justify-center text-xl font-bold px-3 py-3">
                    {i + 1}
                  </div>
                  <div className="flex flex-col truncate">
                    <span className="text-xs text-blue-600 font-bold w-max">
                      {props.questionTypes.questionTypes &&
                        props.questionTypes.questionTypes.find(
                          (itm) => itm.id && itm.id === item.type
                        )?.title}
                    </span>
                    <span className="font-bold">{item.question[0].title}</span>
                    <span
                      className="truncate text-xs"
                      dangerouslySetInnerHTML={{
                        __html: item.question[0].description,
                      }}
                    ></span>
                  </div>
                </div>
              </div>
            ))
          )}
        </>
      )}
    </div>
  );
};

const mapStateToProps = ({
  auth,
  course,
  schoolLevels,
  questionBank,
  prepareExam,
}: StoreState): {
  auth: Auth;
  course: courseStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionBank: QuestionBankStoreInterface;
  prepareExam: PrepareExamsStoreInterface;
} => {
  return {
    auth: auth,
    course: course,
    schoolLevels: schoolLevels,
    questionBank: questionBank,
    prepareExam: prepareExam,
  };
};

const UploadedQuestions = connect(mapStateToProps, {
  loadCourses,
  loadSchoolLevels,
  loadEducationCategories,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  setUploadedQuestions,
  setSelectedCourse,
  addExamQuestion,
  IncrementQuestionBank,
})(_UploadedQuestions);

export default UploadedQuestions;
